.testimonials {
  &__item {
    &:not(:last-child) {
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo {
    width: 100%;
    grid-column: 2 / 4;
    &-container {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 24px;
      transition: transform $transition;

      margin-bottom: 28px;

      @include onDesktop {
        margin-bottom: 32px;
      }

      &:hover {
        transform: scale(1.1);
      }
    }
  }

  &__name {
    margin-bottom: 4px;

    font-weight: 700;
    font-size: 24px;

    line-height: 30px;
    text-align: center;
  }

  &__role {
    margin-bottom: 4px;

    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }
}

.link {
  color: $colorBlue;
  text-decoration: none;
}
