.about {
  &__title {
    margin: 0 0 48px;

    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: -1.5px;
    color: $colorBlack;

    &-link {
      color: $colorBlue;
      text-decoration: none;
    }

    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
      margin: 0;
    }
  }

  &__subtitle {
    display: block;
    margin-bottom: 16px;

    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: $colorBlue;
    text-decoration: none;
  }

  &__paragraph {
    &:not(:last-child) {
      margin-bottom: 27px;
    }

    &--important {
      font-weight: 700;
    }
  }
}
