.form-field {
  background-color: $colorNearWhite;

  border-radius: 8px;
  border: 2px solid transparent;

  display: block;
  width: 100%;
  height: 48px;

  padding: 14px 16px;
  color: $colorBlack;

  &::placeholder {
    font-size: 14px;
    line-height: 20px;
    color: #8f99a8;
  }

  &:hover {
    border-color: #ccd0e0;
  }

  &:focus {
    border-color: $colorBlue;
    outline: none;
  }

  &--textarea {
    height: 144px;
    resize: none;
    margin-bottom: 16px;
  }
}
