.contacts {
  &__info {
    margin-bottom: 24px;

    &-title {
      color: $colorGrey;
      margin-bottom: 8px;
    }
    &-link {
      text-decoration: none;
      color: inherit;
      transition: color $transition;

      &:hover {
        color: #0c9cdf;
      }
    }

    &--last {
      margin-bottom: 32px;
    }
  }

  &__field {
    margin-bottom: 16px;

    &:last-child {
      margin-bottom: 32px;
    }
  }
}
