.place {
  &__photo {
    position: absolute;

    width: 100%;
    height: 100%;

    object-fit: cover;
    object-position: center;

    transition: transform $transition;

    &:hover {
      transform: scale(1.1);
    }

    &-container {
      position: relative;

      width: 100%;
      max-height: 256px;

      overflow: hidden;
      border-radius: 8px;

      &:after {
        content: "";
        display: block;
        padding-bottom: 100%;
      }

      @include onDesktop {
        height: 256px;
        padding-bottom: 0;
      }
    }
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    color: $colorBlue;
    text-decoration: none;
    font-weight: 700;
  }
}
